@import url(https://fonts.googleapis.com/css?family=Open+Sans);
.emailComponent {
  width: 100%; }
  @media screen and (max-width: 1440px) {
    .emailComponent {
      display: flex;
      margin: 20px 110px; } }
  @media screen and (max-width: 1027px) {
    .emailComponent {
      display: flex;
      margin: 20px 90px; } }
  @media screen and (max-width: 768px) {
    .emailComponent {
      display: block;
      margin: 0 auto;
      text-align: center; } }
  @media screen and (max-width: 320px) {
    .emailComponent {
      display: block;
      margin: 0 auto; } }
  .emailComponent .join {
    font-style: normal;
    color: #B6FDBF; }
    @media screen and (max-width: 1440px) {
      .emailComponent .join {
        font-size: 62px;
        margin-right: 68px;
        flex: 1 1; } }
    @media screen and (max-width: 1027px) {
      .emailComponent .join {
        font-size: 60px;
        line-height: 60px; } }
    @media screen and (max-width: 768px) {
      .emailComponent .join {
        font-size: 50px;
        line-height: 60px;
        margin-right: 0;
        margin-bottom: 20px; } }
    @media screen and (max-width: 320px) {
      .emailComponent .join {
        font-size: 36px;
        line-height: 59px;
        margin-right: 0; } }
  .emailComponent .emailForm {
    flex: 3 1; }
    .emailComponent .emailForm .formInstruction {
      font-style: normal;
      color: #94AEBF;
      text-transform: uppercase; }
      @media screen and (max-width: 1440px) {
        .emailComponent .emailForm .formInstruction {
          font-size: 24px;
          line-height: 30px; } }
      @media screen and (max-width: 1027px) {
        .emailComponent .emailForm .formInstruction {
          font-size: 20px;
          line-height: 30px; } }
      @media screen and (max-width: 768px) {
        .emailComponent .emailForm .formInstruction {
          font-size: 20px;
          line-height: 26px; } }
      @media screen and (max-width: 320px) {
        .emailComponent .emailForm .formInstruction {
          font-size: 16px;
          line-height: 20px; } }
    .emailComponent .emailForm #email {
      background: #FFFFFF;
      opacity: 0.1;
      border-radius: 5px;
      display: inline;
      color: #94AEBF;
      font-size: 24px;
      opacity: 0.2;
      line-height: 32px;
      outline: none;
      padding: 1%; }
      @media screen and (min-width: 321px) and (max-width: 1440px) {
        .emailComponent .emailForm #email {
          width: 56%;
          margin-right: 2%; } }
      @media screen and (max-width: 320px) {
        .emailComponent .emailForm #email {
          width: 280px; } }
    @media screen and (min-width: 321px) and (max-width: 1440px) {
      .emailComponent .emailForm .disclamer {
        width: 56%; } }
    .emailComponent .emailForm .disclamer .privacyStatement {
      display: inline;
      color: #888888;
      font-weight: 300;
      line-height: 14px;
      font-size: 12px; }

